html {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: 'Lato', sans-serif;
}

h2 {
  text-align: center;
  margin: 0 40px;
}

.itemList {
  padding: 0;
  list-style-type: none;
  display: flex;
  flex-direction: column;
  grid-gap: 20px;
  gap: 20px;
}

.item {
  display: flex;
  justify-content: space-between;
  padding: 8px 20px;
  grid-gap: 16px;
  gap: 16px;
  align-items: center;
  background-color: lightgreen;
  border-radius: 8px;
  position: relative;
}

.item p {
  flex: 1 1;
}

.item span {
  cursor: pointer;
  font-size: 28px;
}

.item span:first-child {
  color: #fff;
}

.item span.green {
  color: green;
}

.item span:last-child {
  position: absolute;
  top: -12px;
  right: 8px;
}

.item span:last-child:hover {
  color: red;
}

.line {
  text-decoration: line-through;
}

.form {
  display: flex;
  justify-content: center;
  grid-gap: 40px;
  gap: 40px;
  padding: 0 8px;
  margin-top: 20px;
  margin-bottom: 32px;
}

textarea {
  border: 1px solid green;
  outline: none;
  padding: 6px 10px;
}

button {
  background-color: green;
  border: none;
  color: #fff;
  font-weight: bold;
  border-radius: 6px;
  cursor: pointer;
}

.app {
  box-sizing: border-box;
  max-width: 500px;
  margin: 0 auto;
  padding: 30px;
}

